.sidedrawer {
  width: 70%;
  height: 100vh;
  background: brown;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 200;
  transform: translateX(-100%);
  transition: all 0.3s ease-out;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.sidedrawer.show {
  transform: translateX(0);
  background-color: #f4f4f4;
}

.sidedrawer__links {
  display: flex;
  flex-direction: column;
  list-style: none;
}

.sidedrawer__links > li {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
}

.sidedrawer__links > li > a {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  flex: 1;
  padding: 1rem;
  text-decoration: none;
  color: #171717;
  font-size: 1.6rem;
}

.sidedrawer__links > li > a:hover {
  background: #171717;
  color: #f4f4f4;
}

.sidedrawer__links > li > a > span {
  display: flex;
  align-items: center;
  margin-left: 8px;
}

@media (min-width: 960px) {
  .sidedrawer {
    display: none;
  }
}

@media (max-height: 400px) {
  .sidedrawer__links {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
}
